* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
body {
  background-color: bisque;
}
/* .app-container {
} */
.navbar {
  background-color: rgb(228, 199, 163);
}
.navbar-heading {
  color: rgb(0, 0, 0);
  padding: 5px 0px 5px 20px;
  font-size: 1.7rem;
  font-weight: 400;
}
.products-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.product-card {
  /* border: 1px solid rgb(0, 0, 0); */
  width: 350px;
  background-color: rgb(230, 177, 113);
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 0px 2px 5px 2px;
  margin: 20px;
  border-radius: 20px;
  box-shadow: 0px 0px 10px black;
}
.product-card img {
  border-radius: 20px;
  width: 100%;
  height: 200px;
}
.product-upper {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  padding-inline: 5px;
}
.product-discount {
  font-size: 1rem;
  font-weight: 800;
  word-spacing: 1px;
  color: rgb(14, 13, 13);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-rating {
  font-size: 1rem;
  font-weight: 800;
  word-spacing: 1px;
  color: rgb(12, 12, 12);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-mid-1 {
  padding-inline: 5px;
}
.product-description {
  font-size: 0.9rem;
  padding-inline: 5px;
  color: rgb(0, 0, 0);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-mid-2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 5px;
}
.product-price {
  font-size: 1rem;
  font-weight: 600;
  word-spacing: 2px;
  color: rgb(0, 0, 0);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-name {
  font-size: 1rem;
  font-weight: 600;
  word-spacing: 1px;
  color: rgb(0, 0, 0);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-lower {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 5px;
}
.product-stock {
  font-size: 1rem;
  font-weight: 600;
  word-spacing: 1px;
  color: rgb(0, 0, 0);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-category {
  font-size: 1rem;
  font-weight: 600;
  word-spacing: 1px;
  color: rgb(0, 0, 0);
  background-color: rgb(228, 199, 163);
  padding: 5px;
  border: none;
  border-radius: 10px;
}
.product-lower-2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-inline: 5px;
}
.product-delete {
  font-size: 0.8rem;
  font-weight: 600;
  word-spacing: 1px;
  color: rgb(0, 0, 0);
  background-color: rgb(171, 24, 16);
  padding: 5px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.product-update {
  font-size: 0.8rem;
  font-weight: 600;
  word-spacing: 1px;
  color: rgb(0, 0, 0);
  background-color: rgb(45, 166, 15);
  padding: 5px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}
.product-delete:hover {
  background-color: red;
}
.product-update:hover {
  background-color: greenyellow;
}
.add-product-link {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px auto;
  background-color: rgb(236, 194, 97);
  width: 150px;
  text-decoration: none;
  padding: 10px;
  border-radius: 10px;
  font-weight: 600;
  color: black;
  cursor: pointer;
  transition: 0.4s ease-in-out;
}
.add-product-link:hover {
  width: 160px;
  background-color: rgb(246, 176, 89);
}

/* add product css  */
@import url("https://fonts.googleapis.com/css?family=Spartan&display=swap");
* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Spartan";
  color: #2a293e;
}

.contact-us {
  padding: 50px 100px;
  width: 600px;
}

label,
input,
textarea {
  display: block;
  width: 100%;
  font-size: 1rem;
  font-family: "Spartan";
}

input {
  margin-bottom: 10px;
  padding: 8px;
}

h3 {
  font-weight: normal;
  font-size: 10pt;
  line-height: 24pt;
  font-style: italic;
  margin: 0 0 0.5em 0;
}

span {
  font-size: 8pt;
}

em {
  color: #f45702;
  font-weight: bold;
}

input,
textarea {
  border: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  background: #f8f4e5;
  padding-left: 5px;
  outline: none;
  border-radius: 5px;
}

input:focus,
textarea:focus {
  border: 1px solid #c17411;
}

textarea {
  resize: none;
}

button {
  border-radius: 5px;
  display: block;
  float: right;
  line-height: 24pt;
  padding: 0 20px;
  border: none;
  background: #f45702;
  color: white;
  letter-spacing: 2px;
  transition: 0.2s all ease-in-out;
  border-bottom: 2px solid transparent;
  outline: none;
  cursor: pointer;
}
button:hover {
  background: #f45702;
  color: #030303;
  border-bottom: 2px solid #f45702;
}
